@mixin font-face($font-name, $path, $weight: normal, $style: normal, $option: fallback) {
  @font-face {
    font-family: quote($font-name);
    src:
      url(#{$path}.woff2) format("woff2"),
      url(#{$path}.woff) format("woff"),
      url(#{$path}.ttf) format("truetype");
    font-weight: $weight;
    font-style: $style;
    font-display: $option;
  }
}

@include font-face('Mulish', '../fonts/mulish/mulish', 400, 'normal', 'fallback');
@include font-face('Mulish', '..fonts/mulislight/mulishlight', 300, 'normal', 'fallback');
@include font-face('Mulish', '../fonts/mulishbold/mulishbold', 600, 'normal', 'fallback');
