.container {
  display: flex;
}

.form__sidebar {
  width: 325px;
  height: 525px;
  background-color: red;
}

.form__content {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-top: 44px;
  background-color: #e5e5e5;
}

.form__content h2 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 36px;
  /* identical to box height, or 112% */
  color: #FA4616;
  margin-bottom: 22px;
}

.form__content h3 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 25px;
  /* identical to box height */
  margin-bottom: 52px;
  letter-spacing: 0.03em;
}

.from__title span {
  color: #9D71C7;
  padding-left: 8px;
}

.form__checkbox {
  cursor: pointer;
  display: flex;
  padding-left: 35px;
  position: relative;
  user-select: none;
  &:first-child {
    padding-right: 50px;
  }
  input {
    cursor: pointer;
    opacity: 0;
    position: absolute;
    &:checked {
      & ~ .form__checkbox-custom {
        &:after {
          display: block;
        }
      }
    }
  }
  &-custom {
    background-color: #ffffff;
    border: 1px solid #000000;
    border-radius: 4px;
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1), 0 5px 10px 0 rgba(0, 0, 0, 0.05);
    left: 0;
    height: 20px;
    position: absolute;
    top: 0;
    width: 20px;
    &:after {
      content: '';
      border: solid #9D71C7;
      border-width: 0 2px 2px 0;
      display: none;
      height: 12px;
      left: 6px;
      position: absolute;
      top: 2px;
      transform: rotate(45deg);
      width: 7px;
    }
  }
  &-text {
    font-size: 14px;
    line-height: 1.43;
    color: black;
  }
}

.form__checkbox-wrapper {
  display: flex;
  margin-bottom: 35px;
}

.form__header p {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  color: #602C83;
}

.form__header-text {
  display: flex;
}

.form__header {
  display: flex;
  justify-content: space-between;
  background-color: #FAF6FF;
  padding-top: 26px;
  padding-bottom: 25px;
  padding-left: 30px;
  padding-right: 30px;
  margin-bottom: 27px;
  border-radius: 5px;

  svg {
    width: 21px;
    height: 18px;
    margin-right: 6px;
  }
}

.form__wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 950px;
  background-color: white;
  box-shadow: 0px 1px 18px rgba(0, 0, 0, 0.15), 0px 3px 5px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
}

.form__container {
  margin: 0 auto;
  max-width: 950px;
}

.form__main {
  padding-left: 49px;
  padding-right: 50px;
}

.form__inputs-item {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 392px;

  &:first-child {
    margin-right: 50px;
    margin-bottom: 25px;
  }
}

.form__inputs-item input {
  padding-left: 40px;
  border: 1px solid #ACACAC;
  box-sizing: border-box;
  border-radius: 3px;
  padding-top: 5px;
  padding-bottom: 6px;
}

.form__inputs-item input[type="text"] {
  background: url("../images/content/calendar.svg") no-repeat left center;
  background-position-x: 10px;
}

.form__inputs-item input[type="number"] {
  padding-left: 10px;
}

.form__inputs-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 25px;
}

.form__main h3 {
  margin-bottom: 15px;
}

.form__upload .form__group {
  margin-right: 23px;
  margin-top: 13px;
  background-color: #DFDFDF;
  border-radius: 5px;
}

.form__upload h3 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  /* identical to box height */

  display: flex;
  align-items: center;
}

.form__upload input[type="file"] {
  outline: 0;
  opacity: 0;
  pointer-events: none;
  user-select: none;
}

.form__upload .form__upload-label {
  width: 100%;
  max-width: 445px;
  border-radius: 5px;
  display: block;
  padding: 1.2em;
  padding-top: 56px;
  padding-bottom: 28px;
  transition: border 300ms ease;
  cursor: pointer;
  text-align: center;
}

.form__group svg {
  width: 35px;
  height: 39px;
  fill: #5A5A5A;
}

.material-icons {
  transform: rotate(-230deg);
}

.form__upload .form__upload-label i {
  display: block;
  font-size: 52px;
  padding-bottom: 16px;
}

.form__upload .form__upload-label i, .form__upload .form__upload-label .form__upload-span {
  color: #525252;
  transition: 200ms color;
}


.form__upload-wrapper {
  display: flex;
  margin-bottom: 30px;
}

.form__mini-uploads {
  margin-top: 30px;
}

.form__mini-wrapper {
  display: flex;
  margin-bottom: 23px;
  max-width: 327px;
  width: 100%;
  justify-content: space-between;
}

.form__upload-mini .form__upload-label {
  width: 100%;
  max-width: 149px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 25px;
  transition: border 300ms ease;
  cursor: pointer;
  text-align: center;
  background-color: #FFE3DC;

  &:first-child {
    margin-right: 13px;
  }
}

.form__upload-mini .form__upload-span {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;

  color: #FA4616;
}

.form__upload-label span {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  /* identical to box height */

  letter-spacing: 0.03em;
  color: #686868;
}

.form__upload-label p {
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  /* identical to box height */

  letter-spacing: 0.03em;

  color: #525252;
}

.form__upload-mini span {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;

  color: #FA4616;
}

.form__upload-mini .form__upload-label svg {
  width: 20px;
  height: 20px;
  margin-right: 13px;
  fill: #FA4616;
}

.form__upload-mini input[type="file"] {
  outline: 0;
  opacity: 0;
  pointer-events: none;
  user-select: none;
}


.form__upload-text input {
  width: 0;
  height: 0;
}

.form__upload-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form__mini-text p {
  width: 100%;
  max-width: 372px;
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  /* or 150% */
  margin-bottom: 19px;

  color: #7E7E7E;

  &:last-child {
    margin-bottom: 0;
  }
}

.form__mini-text span {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  color: #9D71C7;
  padding-left: 3px;
  padding-right: 3px;
}

.form__textarea {
  display: block;
  height: 100%;
  width: 100%;
  max-width: 844px;
  padding: 0 14px;
  padding-top: 12px;
  padding-bottom: 126px;
  border: 1px solid #999999;
  border-radius: 3px;
}

.form__text {
  margin-bottom: 30px;
}

.form__btn {
  width: 133px;
  height: 46px;
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  /* identical to box height */
  color: white;
  text-align: center;
  letter-spacing: 0.03em;

  background: linear-gradient(185.51deg, rgba(250, 70, 22, 0.47) 4.4%, #FA4616 44.04%);
  border-radius: 3px;
}

.form__back {
  background: white;
  color: #FA4616;
  border: 1.2px solid #FA4616;
  box-sizing: border-box;
  border-radius: 3px;
}

.form__buttons {
  display: flex;
  justify-content: center;
  margin-top: 33px;
  margin-bottom: 40px;
}

.form__buttons-wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 287px;
}

.form__mini-text {
  width: 100%;
  max-width: 372px;
}

@media (max-width: $tablet-width) {
  .form__wrapper {
    max-width: 671px;
  }

  .form__sidebar {
    display: none;
  }

  .form__container h2 {
    font-size: 26px;
    margin-bottom: 0;
  }

  .form__container h3 {
    font-size: 20px;
    margin-bottom: 29px;
  }

  .form__checkbox-text {
    font-size: 12px;
  }

  .form__inputs-item {
    max-width: 279px;

    label {
      font-size: 12px;
      margin-bottom: 4px;
    }
  }

  .form__main {
    padding-right: 33px;
    padding-left: 33px;

    h3 {
      font-size: 14px;
      margin-bottom: 12px;
    }
  }

  .form__inputs-item:first-child {
    margin-right: 46px;
  }

  .form__upload .form__upload-label {
    max-width: 286px;
  }

  .form__upload-mini .form__upload-label {
    padding: 13px 19px;
  }

  .form__mini-text p {
    font-size: 12px;
    max-width: 281px;
    margin-bottom: 16px;
    font-weight: 500;
  }

  .form__mini-wrapper {
    margin-bottom: 15px;
  }

  .form__textarea {
    padding-bottom: 115px;

    &::placeholder {
      font-family: $fontfamali;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 15px;
    }
  }

  .form__text h3 {
    font-size: 12px;
    margin-bottom: 8px;
  }

  .form__upload h3 {
    font-size: 12px;
    margin-bottom: 13px;
  }

  .form__checkbox-wrapper {
    margin-bottom: 50px;
  }

  .form__inputs-wrapper {
    margin-bottom: 52px;
  }

  .form__upload-wrapper {
    margin-bottom: 42px;
  }
}

@media (max-width: $mobile-width) {
  .from__title {
    font-size: 16px;
    max-width: 296px;
  }

  .form__container h2 {
    margin: 0 auto;
  }

  .form__header {
    flex-direction: column;

    p {
      font-size: 18px;
    }
  }

  .form__header-text p {
    font-size: 16px;
  }

  .form__checkbox-wrapper {
    flex-direction: column;
  }

  .form__checkbox {
    &:first-child {
      margin-bottom: 18px;
    }
  }

  .form__inputs-wrapper {
    flex-direction: column;
  }

  .form__inputs-item {
    max-width: 262px;
    margin-bottom: 20px;

    &:first-child {
      margin: 0;
      margin-bottom: 20px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    label {
      font-size: 16px;
      font-weight: bold;
    }

    span {
      font-size: 14px;
      line-height: 18px;

      color: #7A7A7A;
    }
  }

  .form__upload-wrapper {
    flex-direction: column;
  }

  .form__mini-wrapper {
    max-width: 262px;
  }

  .form__upload-mini .form__upload-label {
    padding: 16px 12px;
  }

  .form__textarea {
    max-width: 262px;
    margin: 0 auto;

    &::placeholder {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 18px;
    }
  }

  .form__text h3 {
    margin-left: 11px;
  }

  .form__upload .form__upload-label {
    max-width: 262px;
  }

  .form__mini-text {
    width: 100%;
    max-width: 262px;
  }

  .form__buttons-wrapper {
    flex-direction: column;
    align-items: center;
  }

  .form__btn {
    width: 100%;
    height: 55px;
    max-width: 244px;

    &:first-child {
      margin-bottom: 20px;
    }
  }
}
